.App {
  margin: 2em;
  padding: 0 1em;
  min-width: 32em;
  min-height: 20em;
  color: #333;
  background: white;  
  border: 1px solid #aaa;
  border-radius: 4px;
  -webkit-box-shadow: 0 0 1em hsla(0,0%,0%,0.15);
          box-shadow: 0 0 1em hsla(0,0%,0%,0.15);
}
.App > .todoList > li:nth-child(odd) {
  background: #eee;
}
.App > .todoList > li:nth-child(even) {
  background: transparent;
}



